<template lang="html">
  <div class="convert-point-page">

    <!--  -->
    <default-head header="">  </default-head>

    <form action="#">
      <!--  -->
      <convert-point-ratio></convert-point-ratio>
      <convert-point-input text="" v-model="cashInfo.disamount" data-vv-name="" v-validate="'required'" name="disamount"
      placeholder=""></convert-point-input>
      <convert-point-input inpuType="password" text="" v-model="cashInfo.payPwd" data-vv-name="" v-validate="'required'" name="disamount"
      placeholder=""></convert-point-input>

      <!--  -->
      <foot-btn text="" @click.native="payCashRightNow"></foot-btn>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import convertPointRatio from '@/components/convertPoint/convertPointRatio'
import convertPointInput from '@/components/convertPoint/convertPointInput'
import footBtn from '@/components/common/footBtn'

export default {
  components: { defaultHead, convertPointRatio, convertPointInput, footBtn },
  data () {
    return {
      cashInfo: {
        disamount: this.$route.query.disamount || '',
        payPwd: ''
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      this.ajaxVerifyPayPassword({userId: this.userId})
    },
    payCashRightNow () {
      this.$validator.validateAll().then(() => {
        this.ajaxVerifyPayPasswordIsTrue({
          userId: this.userId,
          payPassword: this.cashInfo.payPwd
        }, () => {
          this.$router.push({
            path: 'payconfirm',
            query: {payPwd: this.cashInfo.payPwd, disamount: this.cashInfo.disamount, cash: this.$route.query.cash || '', type: this.$route.query.type, disamountId: this.$route.query.disamountId || ''}
          })
        })
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    hasPayPwd (path) {
      let self = this
      minutils.MessageBoxConfirm({
        message: ', ',
        options: {confirmButtonText: '', cancelButtonText: ''},
        success (action) {
          self.$router.push(path)
        },
        fail (action) {
          self.$router.go(-1)
        }
      })
    },
    ajaxVerifyPayPassword (data) {
      api.verifyPayPassword(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          !result.data && this.hasPayPwd('/modifypayword')
        }
      })
    },
    ajaxVerifyPayPasswordIsTrue (data, callback) {
      api.verifyPayPasswordIsTrue(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          if (result.data) {
            callback()
          } else {
            minutils.Toast({ message: ', ', position: 'top' })
          }
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.convert-point-page {
  padding-top: .88rem;
  form {
    padding-bottom: .2rem;
    background: @whiteColor;
  }
}
</style>



